@import "constants/Normalized.css";
@font-face {
  font-family: "SUIT-Regular";
  src: url("https://fastly.jsdelivr.net/gh/projectnoonnu/noonfonts_suit@1.0/SUIT-Regular.woff2") format("woff2");
  font-weight: normal;
  font-style: normal;
}
html > body {
  font-family: "SUIT-Regular", sans-serif;
}

.header {
  display: flex;
  align-items: center;
  position: fixed;
  width: 100%;
  height: 6.1rem;
  z-index: 100;
}
.header::after {
  content: "";
  position: absolute;
  top: 100%;
  display: block;
  width: 100%;
  height: 500px;
  max-height: 0px;
  transition: max-height 0.3s;
  background: white;
  border-bottom-right-radius: 50px;
  border-bottom-left-radius: 50px;
  z-index: -1;
}
.header > div {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 100%;
  padding-inline: 4rem;
}
.header > div > .logo:hover {
  cursor: pointer;
}
.header > div > nav:nth-child(2) {
  height: 100%;
}
.header > div > nav:nth-child(2) > ul {
  display: flex;
  margin-right: 15px;
  height: 100%;
  line-height: 1.4;
  font-weight: bold;
}
.header > div > nav:nth-child(2) > ul > li {
  position: relative;
  padding: 0 0.6rem;
}
.header > div > nav:nth-child(2) > ul > li > a {
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: clamp(5.5rem, 10.7vw, 11.5rem);
  font-size: 18px;
  height: 100%;
  text-align: center;
  color: white;
}
.header > div > nav:nth-child(2) > ul > li:hover > a {
  color: #0A31A8;
}
.header > div > nav:nth-child(2) > ul > li > div {
  position: absolute;
  width: 100%;
  height: 500px;
  margin-left: -10px;
  display: none;
}
.header > div > nav:nth-child(2) > ul > li > div.active {
  display: block;
}
.header > div > nav:nth-child(2) > ul > li > div > ul {
  margin-top: 30px;
  text-align: center;
}
.header > div > nav:nth-child(2) > ul > li > div > ul > li {
  line-height: 40px;
  width: 80%;
  margin-inline: auto;
}
.header > div > nav:nth-child(2) > ul > li > div > ul > li > a {
  color: #333333;
}
.header > div > nav:nth-child(2) > ul > li > div > ul > li:hover > a {
  color: #0A31A8;
}
.header > div > nav:nth-child(2) > ul > li > div > ul::after {
  content: "";
  position: absolute;
  top: 38px;
  right: 0;
  display: none;
  width: 1px;
  height: 400px;
  background: rgba(0, 0, 0, 0.1);
}
.header > div > nav:nth-child(3) > ul {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 215px;
}
.header > div > nav:nth-child(3) > ul > li {
  position: relative;
}
.header > div > nav:nth-child(3) > ul > li > a {
  color: white;
}
.header > div > nav:nth-child(3) > ul > li:first-child > a {
  font-weight: 700;
}
.header > div > nav:nth-child(3) > ul > li:first-child > a::after {
  content: "";
  position: absolute;
  top: -3px;
  left: -15px;
  display: block;
  width: 160%;
  height: 30px;
  border-radius: 50px;
  opacity: 0.15;
  background: white;
}
.header > div > nav:nth-child(3) > ul > li:first-child > a:hover::after {
  background: #0A31A8;
  opacity: 1;
  z-index: -1;
}
.header > div > nav:nth-child(3) > ul > li:nth-child(2) {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 60px;
  margin-left: 43px;
}
.header > div > nav:nth-child(3) > ul > li:nth-child(2) > a {
  font-weight: 600;
  font-size: 14px;
}
.header > div > nav:nth-child(3) > ul > li:nth-child(2) > a:last-child {
  color: rgba(255, 255, 255, 0.3);
}
.header > div > nav:nth-child(3) > ul > li:nth-child(2)::before {
  content: "";
  position: absolute;
  left: 30px;
  display: block;
  width: 1px;
  height: 13px;
  background: rgba(255, 255, 255, 0.3);
}
.header > div > nav:nth-child(3) > ul > li:last-child {
  white-space: nowrap;
  overflow: hidden;
  text-indent: 100%;
}
.header > div > nav:nth-child(3) > ul > li:last-child > a::before, .header > div > nav:nth-child(3) > ul > li:last-child > a::after {
  content: "";
  position: absolute;
  display: block;
  right: 5px;
  width: 23px;
  height: 2px;
  background: white;
}
.header > div > nav:nth-child(3) > ul > li:last-child > a::before {
  top: 5px;
}
.header > div > nav:nth-child(3) > ul > li:last-child > a::after {
  top: 12px;
}